.site-header {
  display: block;

  .br {
    display: block;
  }
}


.site-branding {

}

.site-title {
  font-size: 25px;
  line-height: 1;
  margin: 0;
}

.site-suptitle {
  display: inline-block;
  padding: 3px 4px;
  font-size: 12px;
  line-height: 1;
  color: #fff;
  background-color: #31c7cc;
  margin-top: 10px;
}

.main-navigation {

  @media (max-width: @screen-xs-max) {
    font-size: 20px;
    line-height: 1;

    .nav-menu {
      .menu-item {
        float: left;
        margin-right: 10px;

        a {
          display: block;
          padding: 4px 0;
        }
      }
    }
  }

  .nav-menu {

  }

  @media(min-width: @screen-sm-min) {
    a {

      display: block;
      padding: 6px 0;

      color: @text-color;

      &:visited {
        color: @text-color;
      }

      &:hover,
      &:focus,
      &:visited:hover,
      &:visited:focus {
        color: lighten(@text-color, 10%);
      }
    }
  }
}