.f-row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-content: flex-start;

  margin-left:  (@grid-gutter-width / -2);
  margin-right: (@grid-gutter-width / -2);
}

// Row without negative margins
.f-pillar-row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-content: flex-start;
}

.f-row-hor-center {
  justify-content: center;
}

.f-row-ver-center {
  align-items: center;
}

.f-col-no-width {
  position: relative;
  min-height: 1px;
  padding-left:  (@grid-gutter-width / 2);
  padding-right: (@grid-gutter-width / 2);
}

// Создаем базовые стили для всех колонок (padding по бокам, мин. высота и относительное позиционирование)
.make-flex-grid-columns();


// Задаем ширины колонок xs
.make-flex-grid(xs);


// Задаем ширины колонок sm
@media (min-width: @screen-sm-min) {
  .make-flex-grid(sm);
}


// Задаем ширины колонок для md
@media (min-width: @screen-md-min) {
  .make-flex-grid(md);
}


// Задаем ширины колонок для lg
@media (min-width: @screen-lg-min) {
  .make-flex-grid(lg);
}
