.product-items {

}

// f-row
.product-items-list {
  list-style: none;
  padding-left: 0;

  margin-left: -7px;
  margin-right: -7px;
}


// f-col
.product-items-item {
  padding-left: 7px;
  padding-right: 7px;
}

.product-items-item-wrapper {
  border: 1px solid rgba(0, 0, 0, .2);
  border-radius: 3px;
  padding: 9px 15px;

  height: 142px;
  overflow: hidden;

  position: relative;
}

.product-items-item-link-container {
  display: block;

  &,
  &:visited,
  &:hover,
  &:focus {
    text-decoration: none;
  }

  &:hover,
  &:focus {
    border-color: rgba(0, 0, 0, .36);
  }

  .product-items-item-header {
    color: inherit;
  }
}

.product-items-item-header {
  font-size: 25px;
  margin: 0;
  .text-overflow();

  .product-items-item-link-container:hover &,
  .product-items-item-link-container:focus & {
    color: @link-hover-color;
  }
}

.product-items-item-description {
  font-size: 15px;
  line-height: 1.2;
  margin: 2px 0 0 0;
  height: 67px;
  overflow: hidden;
}

.product-items-item-info {
  color: #aaacbc;

  font-size: 13px;
  margin: 0;

  position: absolute;
  bottom: 9px;
  overflow: hidden;

  .ul {
    width: 999px;
  }

  .f-row {
    margin-left: -5px;
    margin-right: -5px;
  }

  .f-col-no-width {
    padding-left: 5px;
    padding-right: 5px;
  }
}