// Huge part of this mixins copied from default Bootstrap grid-framework.less
.make-flex-grid-columns() {
  // Common styles for all sizes of grid columns, widths 1-12
  .col(@index) { // initial
    @item: ~".f-col, .f-col-xs-@{index}, .f-col-sm-@{index}, .f-col-md-@{index}, .f-col-lg-@{index}";
    .col((@index + 1), @item);
  }
  .col(@index, @list) when (@index =< @grid-columns) { // general; "=<" isn't a typo
    @item: ~".f-col, .f-col-xs-@{index}, .f-col-sm-@{index}, .f-col-md-@{index}, .f-col-lg-@{index}";
    .col((@index + 1), ~"@{list}, @{item}");
  }
  .col(@index, @list) when (@index > @grid-columns) { // terminal
    @{list} {
      position: relative;
      // Prevent columns from collapsing when empty
      min-height: 1px;
      // Inner gutter via padding
      padding-left:  (@grid-gutter-width / 2);
      padding-right: (@grid-gutter-width / 2);
    }
  }
  .col(1); // kickstart it
}

// Width
.calc-flex-grid-column(@index, @class, @type) when (@type = width) and (@index > 0) {
  .f-col-@{class}-@{index} {
    width: percentage((@index / @grid-columns));
  }
}


// Basic looping in LESS
.loop-flex-grid-columns(@index, @class, @type) when (@index >= 0) {
  .calc-flex-grid-column(@index, @class, @type);
  // next iteration
  .loop-flex-grid-columns((@index - 1), @class, @type);
}

.make-flex-grid(@class) {
  //.float-grid-columns(@class);
  .loop-flex-grid-columns(@grid-columns, @class, width);
  // Эти стили нам по идее в теме не нужны, но мы можем дописать их в любое удобное время позже
  //.loop-grid-columns(@grid-columns, @class, pull);
  //.loop-grid-columns(@grid-columns, @class, push);
  //.loop-grid-columns(@grid-columns, @class, offset);
}
