.list-dashed {
  list-style: none;
  padding-left: 1.3em;

  li {
    position: relative;

    &:before {
      content: '—';
      margin-left: -1.3em;
      position: absolute;
      left: 0;
      display: block;
    }

    &.list-dashed-none {
      &:before {
        display: none;
      }
    }
  }
}

.text-block-links {
  a {
    display: block;
  }
}
