.calc-flex-grid-column(@index, @class, @type) when (@type = margin-ver) {
  .margin-ver-@{class}-@{index} {
    margin-top: @index;
    margin-bottom: @index;
  }
}

.calc-flex-grid-column(@index, @class, @type) when (@type = margin-ver-important) {
  .margin-ver-important-@{class}-@{index} {
    margin-top: @index !important;
    margin-bottom: @index !important;
  }
}

.calc-flex-grid-column(@index, @class, @type) when (@type = margin-ver-top) {
  .margin-ver-top-@{class}-@{index} {
    margin-top: @index;
  }
}

.calc-flex-grid-column(@index, @class, @type) when (@type = margin-ver-bottom) {
  .margin-ver-bottom-@{class}-@{index} {
    margin-bottom: @index;
  }
}

.make-margins-preset(@class, @size) {
  .calc-flex-grid-column(@size, @class, margin-ver);
}

.make-margins-important-preset(@class, @size) {
  .calc-flex-grid-column(@size, @class, margin-ver-important);
}

.make-margin-top-preset(@class, @size) {
  .calc-flex-grid-column(@size, @class, margin-ver-top);
}

.make-margin-bottom-preset(@class, @size) {
  .calc-flex-grid-column(@size, @class, margin-ver-bottom);
}